import { Injectable, Input } from '@angular/core';

@Injectable({
  providedIn: 'root',
})
export class NotificationService {
  text = '';
  colorError = false;
  showBox: 'on' | 'off' = 'off';

  constructor() {}

  offShow = () => {
    setTimeout(() => {
      this.showBox = 'off';
      this.colorError = false;
    }, 2500);
  };
}
